

import { Component, OnInit, ElementRef, OnDestroy } from '@angular/core';
import{ Router, ActivatedRoute } from '@angular/router'
import { Observable } from 'rxjs';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import { AuthenticationService } from '../../_services';
import { first } from 'rxjs/operators';
import { isArray } from 'util';

declare var $: any;
@Component({
    selector: 'app-Forgotpassword-cmp',
    templateUrl: './Forgotpassword.component.html'
})

export class ForgotpasswordComponent implements OnInit, OnDestroy {
    test: Date = new Date();
    private nativeElement: Node;
    loginForm: FormGroup;
    otpForm:FormGroup;
    loading = false;
    submitted = false;
    returnUrl: string;
    error = '';
    step=1;
    message;
    constructor(
        private element: ElementRef, 
        private formBuilder: FormBuilder,
        private route:ActivatedRoute,
        private router: Router,
        private authService: AuthenticationService
        )
         {
        this.nativeElement = element.nativeElement;
    }

    Login():Observable<any>
    {
    //this.http.post('https://localhost:44337/api/Users/authenticate');
        return null;
    }

    ngOnInit() {
        var navbar : HTMLElement = this.element.nativeElement;
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('login-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card-login')[0];
        setTimeout(function() {
            // after 1000 ms we add the class animated to the login/register card
            // card.classList.remove('card-hidden');
        }, 300);
    this.loginForm=this.formBuilder.group(
    {
        mobileno:['',Validators.required],
        code:['',null],
        password:['', Validators.required],
        cpassword:['', Validators.required],

    });
    this.otpForm=this.formBuilder.group(
        {
            OTP:['',Validators.required],
            id:['', Validators.required],
        });
    this.authService.logout();
    this.returnUrl=this.route.snapshot.queryParams['returnUrl'] || '/home';

    }
    stepthree()
    {
        this.step=3;
        this.loginForm.controls.mobileno.setValue($('#mobileno').val());
    }
    sendcode(){
    var mobileno=$('#mobileno').val();
    if(mobileno==null || mobileno=="")
    {
        return;
    }
    var data={
        phone:mobileno
    }
     this.authService.post("forgotPassword",data).then(
     data=>{
       console.log(data)
       if(data.status==200)
       {
        this.error=null;
        this.step=2;
        this.loginForm.controls.code.setValue(data.data.OTP);
       }
       else{
        this.error=data.message;
       }
         
        
     },
      er=> {
      this.error=er.error.message;
    //   this.phoneerrortext=er.error.message;
    
        //  this.handleError(er.tostring())
     }
    
    );
    
    }
get f() {return this.loginForm.controls;}

onSubmit(){

    this.submitted=true;
    if(this.f.password.value!=this.f.cpassword.value)
    {
     this.error="Password and Confirm do not match!";
    }
    if(this.loginForm.invalid)
    {
        return;
    }
this.loading=true;
var data={
    phone:this.f.mobileno.value,
    password :this.f.password.value
}
console.log(data);
 this.authService.post("updatePassword",data)
 .then(
 data=>{
     this.error=null;
     console.log(data);
     alert(data.message);
     if (data.status == 200)
     {
        this.error =null;
        this.message=data.message;

       this.loading=false;
           setTimeout(() => {
            window.location.href="#/pages/login";
           }, 1500);
       }
       else {
           this.loading=false;
           this.error = data.message;
           setTimeout(() => {
               this.error=null;
           }, 1500);
     }
    
        // this.otpForm.patchValue(data.data);
        // setTimeout (() => {
        //     this.callMyCount();
        //  }, 1000);
    //  this.router.navigate([this.returnUrl]);
 },
 er=> {
     this.loading=false;
     this.error=er;
     console.log(this.error);

    //  this.handleError(er.tostring())
 }

);

}
get ff() { return this.otpForm.controls; }
VerifyOtp(){

    this.submitted=true;
    if(this.otpForm.invalid)
    {
        return;
    }
    localStorage.setItem('currentUser', JSON.stringify("ab")); 
    // this.router.navigate([this.returnUrl]);
    let otp = {
        id: this.ff.id,
        OTP: this.ff.OTP
    };
this.loading=true;
 this.authService.otp(otp.id.value,otp.OTP.value)
 .pipe(first()).subscribe(
 data=>{

        this.step=2;
        console.log(data);
        if (data && data.data.auth_key) {
                    localStorage.setItem('currentUser', JSON.stringify(data.data)); 
                }
         this.router.navigate([this.returnUrl]);
   
 },
 er=> {
     this.loading=false;
     this.error=er;
console.log(this.error);
    //  this.handleError(er)
 }

);


}

VerifyOtpRsend(){
let inputValue = (<HTMLInputElement>document.getElementById("id")).value;
   

 this.authService.otpResend(inputValue)
 .pipe(first()).subscribe(
 data=>{
        this.step=2;
        this.error=data.message;
     //  this.router.navigate([this.returnUrl]);
   
 },
 er=> {
     this.loading=false;
     this.handleError(er)
 }

);


}

callMyCount(){    
    document.getElementById('timer').innerHTML ="01" + ":" + "00";
  
    let timerOn = true;

function timer(remaining) {
  var m = Math.floor(remaining / 60);
  var s = remaining % 60;
  m = m < 10 ? 0 + m : m;
  s = s < 10 ? 0 + s : s;
  document.getElementById('timer').innerHTML = m + ':' + s;
  remaining -= 1;
  
  if(remaining >= 0 && timerOn) {
    setTimeout(function() {
        timer(remaining);
    }, 1000);
    return;
  }

  if(!timerOn) {
    // Do validate stuff here
    return;
  }
var element = <HTMLInputElement> document.getElementById("btnresent");
element.disabled = false;
  // Do timeout stuff here
}

timer(5);
  }
handleError(error)
    {
        this.loading=false;
         
        this.error=error;
    }

    ngOnDestroy(){
      const body = document.getElementsByTagName('body')[0];
      body.classList.remove('login-page');
      body.classList.remove('off-canvas-sidebar');
    }
}


